import axios from 'axios'
import jsonp from 'common/js/jsonp'
// 
import site from './config'

axios.defaults.baseURL = 'http://120.79.162.149:3000'

export default {
  /*
    @method 
   */
  banner () {
    return jsonp(site.banner, {
      g_tk: 1716874572,
      uin: 0,
      format: 'jsonp',
      inCharset: 'utf-8',
      outCharset: 'utf-8',
      notice: 0,
      platform: 'h5',
      needNewCode: 1
    }, {
      param: 'jsonpCallback'
    })
  },
  /*
    @method 
   */
  remdList () {
    return axios.get(site.personalized)
  },
  /*
    @method 
    @param: id
   */
  remdListDetail (id) {
    return axios.get(site.playlistDetail, {
      params: {
        id
      }
    })
  },
  /*
    @method 
   */
  newSong () {
    return axios.get(site.newSong)
  },
  /*
    @method 
   */
  singerRank () {
    return axios.get(site.singerRank)
  },
  /*
    @method 
    @param: id
   */
  getArtists (id) {
    return axios.get(site.artists, {
      params: {
        id
      }
    })
  },
  /*
    @method 
    @param: id
   */
  getMusicUrl (id) {
    return axios.get(site.musicUrl, {
      params: {
        id
      }
    })
  },
  /*
    @method 
    @param: id
   */
  getMusiclyric (id) {
    return axios.get(site.lyric, {
      params: {
        id
      }
    })
  },
  /*
    @method 
    @param idx
    "0": ,
    "1": ,
    "2": ,
    "3": ,
    "4": ,
    "5": UK,
    "6": Billboard
    "7": KTV,
    "8": iTunes,
    "9": Hit FM Top,
    "10": Oricon
    "11": Melon,
    "12": Mnet,
    "13": Melon,
    "14": TOP(),
    "15": TOP()
    "16": ,
    "17": ,
    "18": ,
    "19":  NRJ EuroHot 30,
    "20": Hito,
    "21": Beatport,
    "22": ACG,
    "23": 
   */
  getRankList (idx) {
    return axios.get(site.rankList, {
      params: {
        idx
      }
    })
    // return axios.get(site.rankList)
  },
  /*
    @method 
   */
  getHotKey () {
    return jsonp(site.hotKey, {
      g_tk: 1888361161,
      uin: 0,
      format: 'jsonp',
      inCharset: 'utf-8',
      outCharset: 'utf-8',
      notice: 0,
      platform: 'h5',
      needNewCode: 1
    }, {
      param: 'jsonpCallback'
    })
  },
  /*
    @method 
    @param: keywords
   */
  search (keywords, page) {
    return axios.get(site.search, {
      params: {
        keywords,
        offset: page * 30
      }
    })
  },
  /*
    @method 
    @param: keywords
    @param: offset  
   */
  suggest (keywords) {
    return axios.get(site.searchSuggest, {
      params: {
        keywords
      }
    })
  },
  // 
  getSearchSinger(name,type){
    return axios.get(site.search, {
      params: {
        name,
        type
      }
    })
  },
  // 
  getSongDetail(ids){
    return axios.get(site.songDetail, {
      params: {
        ids
      }
    })
  }
 
}



