<template>
    <!--  -->
    <div ref="wrapper">
        <slot></slot>
    </div>
</template>
<script>
import BScroll from 'better-scroll'
// import { setTimeout } from 'timers';
export default {
    props: {
        probeType:{
            tupe:Number,
            default:1 
            // 1-()scrool
            // 2-(scrool
            // 3- momentum  scroll 
        },
        click:{
            type:Boolean,
            default:true
        },
        data:{
            type:Array,
            default:null
        },
        // scroll
        listenScroll:{
            type:Boolean,
            default:false 
        },
        refreshDelay: {
            type: Number,
            default: 20
        },
        // 
        pullup:{
            type:Boolean,
            default:false
        },
        beforeScroll:{
            type: Boolean,
            default: false
        }

    },
    mounted () {
        setTimeout(()=>{
            this._initScroll()  
        },this.refreshDelay)
    },
    methods: {
        _initScroll(){
            if(!this.$refs.wrapper){
                return
            }
            this.scroll = new BScroll(this.$refs.wrapper,{
                probeType:this.probeType,
                click:this.click
            })
            if(this.listenScroll){
                let _this = this
                this.scroll.on('scroll', (pos) => {
                   _this.$emit('scroll', pos)
                })
            } 
            // 
            if(this.pullup){
                this.scroll.on('scrollEnd',()=>{
                    if(this.scroll.y <= (this.scroll.maxScrollY +50)){
                        this.$emit('scrollToEnd')
                    }
                })
            }
            // 
            if(this.beforeScroll){
                this.scroll.on('beforeScrollstart',()=>{
                    this.$emit('beforeScroll')
                })
            }
        },
        enable(){
            // better-scrollenable
            this.scroll && this.scroll.enable()
        },
        disable(){
            // better-scrolldisable
            this.scroll && this.scroll.disable()
        },
        // scroll
        refresh(){
             // better-scrollrefresh
            this.scroll && this.scroll.refresh()
        },
        scrollTo(){
            //
            this.scroll && this.scroll.scrollTo.apply(this.scroll,arguments)
        },
        scrollToElement(){
            //
            this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
        },
        // 
    },
    watch: {
        data(){
            setTimeout(()=>{
                this.refresh()
            },this.refreshDelay)
        }
    }
}
</script>




